:root {
    --cor-principal: #2f3132;
    --cor-secundaria: #d8a15b;
    --cor-terciaria: #e3b67a;
    --cor-texto: #333;
    --cor-fundo: #8c8e8f;
    --cor-fundo-div: #f0f0f0de;
}


main {
    height: 100vh;
    width: 100vw;

}


.boasvindas {
    margin: 30px auto 0;
    width: 80%;
    font-size: 22px;
    font-weight: bold;

    animation: showUp 0.7s ease-in-out;
}


/* Primeira div */
.firstDiv {
    position: relative;
    top: 100px;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;

    height: auto;

    display: flex;
    justify-content: center;
    align-items: center;

    animation: showUp 1s ease-in-out;
}

.firstDiv p {
    margin: 30px 0% 20px;
    font-size: 25px;
}

.firstDiv div {
    height: auto;
    border: #2f3132 2px solid;
    background-color: var(--cor-fundo-div);
    border-radius: 20px;
    margin-bottom: 20px;
}

.firstDiv ul {
    height: 80%;
    gap: 5px;
    margin: 0px 0 30px;
}

.firstDiv button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 250px;
    height: 50px;

    border-radius: 25px;
    cursor: pointer;
    font-size: 17px;
    font-weight: bold;
    box-shadow: 0px 4px 8px #2f3132;

    transition: transform 0.4s ease-in-out;
    transform: translateY(0);

}

.firstDiv button:hover {
    transform: translateY(-2px) scale(1.05);
    transition: transform 0.3s ease-out;
    color: #FFF;
}

.tempoVerbal-btn {
    background-color: var(--cor-secundaria);
}

.pronomes-btn,
.modalVerbs-btn,
.numbers-btn {
    background-color: var(--cor-terciaria);
}

/* Tempo Verbal */

.container {
    height: 500px;
    width: calc(100vw - 20px);
    margin: 0;
    position: relative;
    top: 150px;

}

.botoes-container {
    display: flex;
    justify-content: center;
    gap: 40px;
}

.botoes-container.animate {
    animation: pop 0.5s ease-in-out forwards;
}

/* Para todos os botoes do tempo verbal */

.botoes-container button {
    height: 50px;
    width: 200px;
    margin-bottom: 40px;
    border: var(--cor-principal) 2px solid;
    border-radius: 25px;
    font-size: 18px;
    font-weight: bold;
    background-color: #e3b67a;
    cursor: pointer;
    box-shadow: 0px 3px 5px var(--cor-texto);
    transition: transform 0.4s ease-in-out;
    transform: translateY(0);
}

.botoes-container button:hover {
    transform: translateY(-2px) scale(1.05);
    transition: transform 0.3s ease-out;
    background-color: var(--cor-secundaria);
}

.botoes-container button:active {
    background-color: var(--cor-terciaria);
}

.tempoVerbal {
    display: flex;
    justify-content: center;
    font-size: 25px;
    padding-bottom: 5px;
    margin: 0 auto 20px;
    width: 300px;
    border-bottom: #000 2px solid;
}

.tempoVerbal.animate {
    animation: showUp 0.8s ease;
}

/* Cada botao do tempo verbal */
.simplePast-btn:hover {
    color: #ff0000b4;
}

.presentContinuous-btn:hover {
    color: #eeff00b4;
}

.simplePresent-btn:hover {
    color: #15ff00b4;
}

.simpleFuture-btn:hover {
    color: #0011ffb4
}


.subDivs.animate,
.container-modal.animate {
    animation: showUp 0.8s ease;

}



.subDivs {
    display: flex;
    flex-direction: row;
    width: 1200px;
    justify-content: center;
    gap: 20px;

}

.coluna {
    width: 30%;
    height: 350px;

    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    border: var(--cor-principal) 2px solid;
    border-radius: 20px;
    background-color: #fefefe80;
    padding: 0px 15px;
    box-shadow: 0px 4px 8px #2f3132;
    margin-bottom: 50px;
}

.titulo {
    font-size: 20px;
    font-weight: bold;
    margin: 10px 0 15px;
    color: #FFF;
    text-shadow: 0px 2px 3px #000;

}

.explicacao {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-weight: bold;
}




/* PRONOMES */

hr {
    border: none;
    background-color: #000;

    margin: 10px 0 10px;
}

.button-pronomes,
.button-modal {
    height: 200px;
    top: 100px;

    flex-wrap: wrap;
    width: 700px;
    margin: 0 auto;
}


.button-pronomes.animate,
.button-modal.animate,
.button-numbers.animate {
    animation: pop 0.5s ease-in-out forwards;
}

.button-pronomes ul,
.button-modal ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;

}

.button-pronomes button,
.button-modal button,
.button-numbers button {
    height: 50px;
    width: 150px;
    border-radius: 25px;
    background-color: var(--cor-terciaria);
    cursor: pointer;
    transition: transform 0.4s ease-in-out;
    transform: translateY(0);
    font-size: 15px;
    font-weight: bold;
    box-shadow: 0px 3px 5px #2f3132;
    padding: 0 20px;

}

.button-pronomes button:hover,
.button-modal button:hover,
.button-numbers button:hover {
    background-color: var(--cor-secundaria);
    transform: translateY(-2px) scale(1.05);
    transition: transform 0.3s ease-out;
    color: #FFF;
}

.container-pronomes,
.container-modal {
    border: #000 solid 1px;
    border-radius: 20px;
    width: 700px;

    margin: 0 auto 0;
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    height: 300px;


    position: relative;
    top: 100px;

    background-color: #fefefe80;
    box-shadow: 0px 4px 8px #2f3132;

}

.container-pronomes.animate,
.container-numbers.animate {
    animation: showUp 0.6s ease;
}



.container-content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 100%;
}


.container-pronomes strong,
.container-content-modal strong {
    color: var(--cor-secundaria);
    text-shadow: 0px 1px 3px #000;
}

.tipePronouns,
.tipeModal {
    position: absolute;
    top: 15px;
    left: 15%;
    text-shadow: 0px 1px 1px #000;
    width: 500px;
    text-align: center;
}

.pronouns1 {
    display: flex;
    flex-direction: column;
    padding: 30px 15px 10px 30px;
    gap: 70px;

}

.pronouns2,
.modal1 {
    display: flex;
    flex-direction: column;
    margin-right: 5px;

}

.pronouns3,
.modal2 {
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}

.pronouns4,
.modal3 {
    display: flex;
    flex-direction: column;
    margin-right: auto;
}

.pronouns1Demonstrative {
    display: flex;
    flex-direction: column;
    padding: 30px 15px 30px 30px;
    gap: 20px;
}


/* Modal Verbs */

.button-modal,
.button-numbers {
    height: 150px;
}

.tipeModal {
    left: 180px;
}

.container-modal {
    width: 800px;
    height: 300px;

    margin: 0 auto;
    padding: 0 10px;
}

.modal-line {
    display: flex;
    flex-direction: row;
}

.container-content-modal {
    display: flex;
    justify-content: center;

    width: 100%;
}

.modal1 {
    margin: 0 10px 0 10px;
    height: 100%;
}

.modal2 {
    margin: 0 10px 0 0;

}

/* Number (Cardinal and Ordinal)  =======================================================*/
.button-numbers {
    width: 300px;
    height: 50px;
    margin: 0 auto;
    top: 150px;
}

.button-numbers ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 50px;
}

.container-numbers {
    display: flex;
    flex-direction: column;
    border: #000 1px solid;
    border-radius: 20px;
    background-color: #fefefe80;

    width: 800px;
    height: 300px;

    margin: 0 auto 100px;
    gap: 0;
    top: 200px;

}

.container-content-numbers {
    display: flex;
    width: 100%;
    height: 100%;


}

.tipeNumebers {
    margin-top: 20px;

}

.cardinal1,
.cardinal2,
.cardinal3,
.cardinal4 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.cardinal1 {
    width: 50%;
}

.cardinal2 {
    width: 50%;
}

.cardinal3 {
    width: 50%;
}

.cardinal4 {
    width: 70%;
}


/* Animaçoes */
@keyframes pop {
    0% {
        transform: scale(1);

    }

    50% {
        transform: scale(1.05);
    }

    100% {
        transform: scale(1);

    }
}

@keyframes showUp {
    0% {
        transform: translateY(-30px);
    }

    50% {
        transform: translateY(10px);
    }

    100% {
        transform: translateY(0px);
    }
}

footer {
    height: 100px;
}

@media (max-width: 1600px) {

    /* PRONOMES */
    .container-pronomes {
        width: 850px;
        height: 350px;
    }

    .tipePronouns {
        left: 23%;
    }

    /* MODAL VERBS */
    .container-modal {
        height: 350px;
    }

    .container-content-modal {
        margin-top: 20px;
    }

    /* Numbers */
    .container-numbers {
        width: 900px;
        height: 350px;
    }
}

@media (max-width: 1024px) {
    .subDivs {
        width: 1000px;
    }
}

@media (max-width: 480px) {

    /* TEMPO VERBAL */
    .subContainer {
        margin-top: 900px;
    }

    .botoes-container {
        width: 500px;
        flex-wrap: wrap;
        margin: 0 auto;
        gap: 20px;
    }

    .botoes-container button {
        margin-bottom: 0px;
    }

    .tempoVerbal {
        margin-top: 20px;
    }

    .subDivs {
        flex-direction: column;
        margin: 0 auto;
        width: 1000px;
        align-items: center;
        gap: 0;
    }

    /* PRONOMES */
    .button-pronomes {
        width: 450px;
        margin: 30px 0 100px;
    }

    .container-pronomes {
        width: 450px;
        height: 430px;
    }

    .Singular {
        position: absolute;
        transform: rotate(270deg);
        top: 150px;
        left: -25px;
    }

    .Plural {
        position: absolute;
        transform: rotate(270deg);
        top: 270px;
        left: -10px;
    }
    .tipePronouns{
        left: -5px;
    }
    .pronouns2{
        margin: 0 30px 0 50px;
    }
    .pronouns3{
        margin-right: 50px;
    }
    .pronouns4{
        display: none;
    }
    .SinPlr{
        position: absolute;
        transform: rotate(270deg);
        left: -80px;
    }

    /* MODAL VERBS */
}